.Punnett {
    width: 60%;
    border: 3px solid red;
    background-color: #c6c6c6;
}

.Punnett-p {
    /* color: white; */
}

.diagram-row {
    display: flex;
    flex-flow: row;
    justify-content: center;
    margin: 0px 5px;
    padding: 0px 2px;
}

.top {
    height: 7vw;
}

.filler {
    width: 13vw;
    height: 13vw;
}

.text-row {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    height: 3em;
    /* margin: 0px; */
}

.diagram-column {
    display: flex;
    flex-flow: column;
    margin: 0px 5px;
}

.left {
    margin-top: 8vw;
}

.text-column {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    height: 26vw;
    margin-top: 7vw;
}

.smallsquare {
    margin: 5px;
    width: 7vw;
    height: 7vw;
    border-radius: 10px;
}

.smallsquare img {
    width: 3.5vw;
}

.smallsquare div {
    font-weight: 600;
    height: 1em;
    color: #585858;
}

.bigsquare {
    margin: 1px;
    width: 13vw;
    height: 13vw;
}

.bigsquare img {
    width: 8vw;
}

.bigsquare div {
    font-weight: 800;
    height: 4em;
    color: #585858;
}

.selected {
    border: 3px solid #e07567;
}