body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1,
h2,
h3,
p {
  color: #ffffff;
}

.App {
    text-align: center;
    width: 100%;
    background-color: #D3D3D3;
    min-height: 100vh;
    display: -webkit-flex;
    display: flex;
}

.App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 40vmin;
    pointer-events: none;
}

.App-header {
    background-color: #282c34;
    /* min-height: 100vh; */
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
.Punnett {
    width: 60%;
    border: 3px solid red;
    background-color: #c6c6c6;
}

.Punnett-p {
    /* color: white; */
}

.diagram-row {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row;
            flex-flow: row;
    -webkit-justify-content: center;
            justify-content: center;
    margin: 0px 5px;
    padding: 0px 2px;
}

.top {
    height: 7vw;
}

.filler {
    width: 13vw;
    height: 13vw;
}

.text-row {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    height: 3em;
    /* margin: 0px; */
}

.diagram-column {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: column;
            flex-flow: column;
    margin: 0px 5px;
}

.left {
    margin-top: 8vw;
}

.text-column {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    height: 26vw;
    margin-top: 7vw;
}

.smallsquare {
    margin: 5px;
    width: 7vw;
    height: 7vw;
    border-radius: 10px;
}

.smallsquare img {
    width: 3.5vw;
}

.smallsquare div {
    font-weight: 600;
    height: 1em;
    color: #585858;
}

.bigsquare {
    margin: 1px;
    width: 13vw;
    height: 13vw;
}

.bigsquare img {
    width: 8vw;
}

.bigsquare div {
    font-weight: 800;
    height: 4em;
    color: #585858;
}

.selected {
    border: 3px solid #e07567;
}
